@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700&display=swap);



.loading--container{
  display:flex;
  flex-direction: column;
  
  align-items: center;
}
.loader {
  
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.yellow--top{
    color: black;
    min-height: 50px;
    background: var(--main-yellow);
    padding: 1em;
    font-size: 1.1rem;
    text-align: center;
    border-radius: 10px;
}

.info--container{
  width: 100%;
  display: flex;
  justify-content: center;
  min-height: 560px;
  margin: 1em 0;
  color: white;
  padding: 1em;
}

.info--header{
  display: flex;
  justify-content: space-between;
}
.info--not--loaded{
  margin: 1em 0;
}

.random--btn{
  
  padding: 0.5em 1.4em;
  font-size: 1.4rem;
  background: var(--btn-grey);
  color: var(--almost-white);
  border-radius: 10px;
  box-shadow: 0px 0px 0px transparent;
  border: 2px solid var(--btn-border);
  text-shadow: 0px 0px 0px transparent;
  transition: color 250ms ease-in-out;
}

.random--btn:hover{
  cursor: pointer;
  color: var(--main-yellow);
  
  
  
}



.info--box{
  
  max-width: 100ch;
  padding: 1em
  
}
.info--header{
  margin-left: 0.5em;
}

.info--header--name{
  padding: 0.3em 1em;
  background: var(--chart-background);
  border: 1px solid var(--main-yellow);
  border-radius: 10px;
}
.info--header--id:before{
  content: "#"
}
.info--header--id{
  margin-right: 0.5em;
}
.info--grid{
  display: grid;
  grid-template-areas: "image stats"
                       "image stats"
                       "image stats"
                       "image info"
                       "footer footer";
  grid-gap: 1em;
  gap: 1em;
}

.info--pokemon--image img{
max-width: 80%;


}
.type{
  display: inline-block;
  padding: 0.2em 1.4em;
  border-radius: 10px;
  color: #000;
  margin: 0 0.5em 0.5em 0.5em; 
  font-size: 1.5rem;
  font-weight: 500;
}

.white--text{
  color: #eee;
  
}
.info--pokemon--image{
  grid-area: image;
  display: flex;
  justify-content: center;
  min-width: 395px;
  

}
.info--pokemon--info{
  padding: 1em;
  font-size: 1.3rem;
  grid-area: info;
  border-radius: 10px;
  background: var(--btn-grey);
  border: 2px solid var(--btn-border)
  
}

.info--pokemon--stats{
  grid-area: stats;
}

.info--pokemon--footer{
  grid-area: footer;
  display: flex;
  justify-content: left;
}



@media only screen and (max-width: 1024px)
{
  .info--header{
    display: flex;
    justify-content: center;
  }
  .info--grid{
    
    grid-template-areas: "image image"
                         "stats stats"
                         "stats stats"
                         "info info"
                         "footer footer";
  }

  .info--pokemon--image img{
    max-width: 220px;
    min-width: 150px;
    margin-left: auto;
  margin-right: auto;
  display: block;

    }
}






.chart{
  border-radius: 10px;
  display: flex;
  justify-content: center;
  border-left: 2px solid var(--btn-border);
  border-bottom: 2px solid var(--btn-border);
  padding: 0.3em;
  background: var(--chart-background);
  
  min-width: 100%;
}

.bar{
  border-bottom: 1px solid var(--btn-border);
  width: 100%;
  height: calc(100%/10) 
}

.bar--container{
  
  width: 60px;
  position: relative;
  overflow: none;
  list-style-type: none;
  
}

.value{
  width: 100%;
  position: absolute;
  background: var(--chart);
  bottom: 0;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  box-shadow: 2px 1px 4px black;
  color: black;
  font-weight:500;
  font-size: 0.9rem;
  
}

.chart--text{
  position: absolute;
  top: -15px;
  left: 50%;
  -webkit-transform: translate(-50%,0);
          transform: translate(-50%,0);
  padding: 0.1em 0.5em;
  background: var(--almost-white);  
  box-shadow: 1px 1px 3px 1px black;
  border-radius: 3px;
}
.special--attack--bar{
  height: 0;
  transition: height 500ms cubic-bezier(.8,.45,.25,1.64) 0.35s;
}

.hp--bar{
  height: 0;
  transition: height 800ms cubic-bezier(.8,.45,.25,1.64) 0.4s;
}

.attack--bar{
  height: 0;
  transition: height 500ms cubic-bezier(.8,.45,.25,1.64) 500ms;
}

.defence--bar{
  height: 0;
  transition: height 500ms cubic-bezier(.8,.45,.25,1.64) 0.6s;
}

.speed--bar{
  height: 0;
  transition: height 900ms cubic-bezier(.8,.45,.25,1.64) 0.25s;
}

.second--ul{
  
  margin: 0;
  padding: 0;
  width: 100%;
  overflow: hidden;
  border: 1px solid var(--btn-border);
  height: 150px;
}

.secondary--ul{
  
  margin: 0;
  padding: 0;
  width: 100%;
}

.main--ul{
  
  margin: 0.5em 0.3em;
  padding: 0;
}

.chart--and--text{
  text-align: center;
  font-size: 1rem;
  
  
}
.four0four
{
  color: var(--almost-white);
  text-align: center;
}

.four0four img{
  max-width: 40%;
}

@media only screen and (max-width: 1024px)
{
  .yellow--top h1{
    font-size: 1.3rem;
    
  } 
