.pokemoncard{
  position: relative;
  display: flex;
  width: 100%;
  background: #ffcd00;
  border: 1px solid #cdcfcf;
  flex-direction: column;
  font-family: var(--font-family);  
  min-width: 225px;
  max-width: 225px;
  flex-basis: 20%;
  transition: margin 400ms ease-in-out 0ms, -webkit-transform 200ms ease-in-out;
  transition: margin 400ms ease-in-out 0ms, transform 200ms ease-in-out;
  transition: margin 400ms ease-in-out 0ms, transform 200ms ease-in-out, -webkit-transform 200ms ease-in-out;
  padding: 0.2em;
}

.pokemon--container{
  border-radius: 5px;
  background-image: url(/static/media/card-background.e9c7a814.png);
  margin: 5px;
  padding: 0.1em;
  border: 1px solid var(--btn-border);
}

.pokemoncard:hover .pokemon--container{
  cursor: pointer;
  border-color: var(--black);
}

.pokemoncard:hover .pokemon--id{
  -webkit-transform: translate(0, 100px);
          transform: translate(0, 100px);
}

.pokemoncard:hover .pokemon--hp{
  -webkit-transform: translate(100px,0);
          transform: translate(100px,0)
}

.pokemoncard:hover ~pokemon--hp{
  -webkit-transform: translate(100px,0);
          transform: translate(100px,0)
}

.pokemoncard:hover{
  margin: 0 10px;
  box-shadow: 0px 0px 5px 2px white;
  -webkit-transform: translate(0, -10px);
          transform: translate(0, -10px);
}


.image--container{
  display: flex;
  align-self: stretch;
  justify-content: center;
}
.card--picture{
  max-width: 75%;
  
}

.card--header{
  position: relative;
  display: flex;
  justify-content: space-between;
  font-size: 1.2rem;
  margin: 2px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: black;
}

.pokemon--id{
  
  position: absolute;
  
  top:-100px;
  right:0;
  background: rgba(0,0,0,0.2);
  padding: 0 0.5em;
  transition: -webkit-transform ease-in-out 400ms;
  transition: transform ease-in-out 400ms;
  transition: transform ease-in-out 400ms, -webkit-transform ease-in-out 400ms;
  border-radius: 5px;
}
.pokemon--hp{
  
  position: absolute;
  top:0px;
  right:0;
  background: rgba(0,0,0,0.2);
  padding: 0 0.5em;
  transition: -webkit-transform ease-in-out 400ms;
  transition: transform ease-in-out 400ms;
  transition: transform ease-in-out 400ms, -webkit-transform ease-in-out 400ms;
  border-radius: 5px;
}

.card--footer{
  text-align: center;
  font-size: 1rem;
  background: var(--card-footer);
  padding: 0.3em;
  color: var(--almost-white);
}

.stats{
  font-size: 1rem;
  font-weight: 500;
  padding: 1em;
  background: rgba(0,0,0,0.2);
  color: black;
  margin-bottom: 5px;
  border-radius: 5px;
}

.stat--item{
  display: block;
  font-size: 0.9rem;
  font-weight: 700;
}
.favourites{
  background: var(--btn-grey);
  min-height: 250px;
  padding: 1em;
}
.pokecards{
  display: flex;
  margin-top: 1em;
  padding: 1em;
  grid-gap: 1em;
  gap: 1em;
  flex-wrap: wrap;
  justify-content:center
}

.pokemonNavigation{
  display: flex;
  justify-content:center;
}



.arrow--size{
  font-size: 8rem;
  color: var(--card-footer);
  opacity: 0.3;
  transition: opacity 300ms ease-in-out;
  
}

.arrow--size:hover{
  opacity: 1;
}

.detailsview--pokemon--container{
  
  display: flex;
  justify-content: space-between;
}

.arrow{
  align-self: center;
}

header li{
  display: inline-block;
  list-style-type: none;
  margin: 0.5em 0.3em;
  font-family: sans-serif;
  background-color: var(--btn-grey);
  font-size: 1em;
  border: 2px solid var(--btn-border);
  border-radius: 5px;
  position: relative;
  
  }
  
  header li::after{
  content: "";
  background: var(--main-yellow);
  height: 0.2em;
  width: 100%;
  display: block;
  position: absolute;
  bottom: -10px;
  -webkit-transform: scale(0);
          transform: scale(0);
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transition-duration: 0.1s;
  }
  
  header li::before{
    content: "";
    background: #ffcd00;
    height: 0.2em;
    width: 100%;
    display: block;
    position: absolute;
    top: -10px;
    -webkit-transform: scale(0);
            transform: scale(0);
    transition-property: -webkit-transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
    transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
    transition-delay: 0.1s;
    transition-duration: 0.15s;
    
    }
  
  header li:active, header li:hover::after, header li:hover::before{
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  
  .menulink{
    display: inline-block;
    padding: 0.2em 1.5em;
    color: white;
    text-decoration: none;
    cursor: pointer;
  }
  
  .menulink:active, .menulink:hover{
    color: #ffcd00;
    transition: color linear 350ms;
  }
