:root{
  /* FONTS */
--font-family: "lato", sans-serif;
--font-weight-bold: "700";
--font-weight-regular: "400";
--font-weight-thin: "400";

/* COLORS */
--main-yellow: #ffcd00;
--dark-grey: #222;
--btn-grey: #555;
--chart-background: linear-gradient(45deg,#666, #333);
--card-footer: #994400;
--dark-brown-hover: #662200;
--btn-border: grey;
--black: black;
--almost-white: #ededed;
--light-grey: #cecece;
--chart: linear-gradient(#ffcd00, #994400);
--btn-hover-fx: linear-gradient(45deg,#8899ff, #aabb22, #ddbb55, #994400);

}
html{
  overflow-y: scroll;
}

body{
  background-color: #222;
  background-color: var(--dark-grey);
  margin: 0;
  padding: 0;
}

img{
  max-width: 100%;
}

*, *::before, *::after{
  box-sizing: border-box;
}

@media only screen and (max-width: 1024px){
.footer{
  min-width: 100%;
  
  margin: 0 0 1em 0;
}

  
}
